<template>
    <div class="mypanel">

        <div class="hd">
            优惠劵管理
        </div>

        <!-- 搜索控件开始 -->
        <div class="search-options">


            <table cellspacing="0" width="100%">
                <tbody>

                <tr>
                    <td>


                      名称:  <input name="name" @keyup.enter="search" v-model="name" class="input-txt" type="text"/>


                        <el-button type="success" icon="el-icon-search" @click="search">搜索</el-button>
                    </td>
                </tr>
                </tbody>
            </table>


        </div>
        <!-- 搜索控件结束 -->


        <div class="action-details">
            <el-button-group>
                <!--{{export-view}}-->
                <el-button type="danger" icon="el-icon-delete" @click="deleteRec">删除</el-button>
                <route-button icon="el-icon-plus" url="/admin/couponadd">新增</route-button>

            </el-button-group>
        </div>

        <div class="coupon-list">


                <div v-for="(coupon,index) in  listCoupon" :key="coupon.id" class="coupon coupon01">
                    <div class="left-ar">
                        <div class="title">
                            <input class="check" name="ids" type="checkbox" v-model="coupon.rowSelected"/>

                            {{coupon.name}}</div>
                        <div class="fee-ar">
                            <div class="unit">¥</div>
                            <div class="fee">{{coupon.fee}}</div>
                            <div class="text">优惠卷</div>
                        </div>
                        <div class="des">订单满{{coupon.minreq}}送{{coupon.fee}}</div>
                    </div>
                    <div class="right-ar">
                        <div class="title">
                            <router-link :to="'/admin/couponadd?id='+coupon.id" class="el-button el-button--small el-button--primary">编辑</router-link>
                        </div>
                        <div class="time">

                        </div>
                        <div class="time">
                            {{coupon.overdate}}

                        </div>
                    </div>
                </div>

        </div>


        <el-pagination
            background
            @size-change="pagesizeChange"
            @current-change="pageindexChange"
            :current-page="pageindex"
            :page-sizes="[pagesize]"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>

        <el-dialog :visible.sync="infodlg" title="优惠劵详细">

            <table class="smart-table" border="1" cellspacing="1" width="100%">


                <tr>
                    <td class="tlabel" width="10%" align="right">备注:</td>
                    <td width="*">
                        {{coupon.name}}
                    </td>
                </tr>


                <tr>
                    <td class="tlabel" width="10%" align="right">面值:</td>
                    <td width="*">
                        {{coupon.fee}}
                    </td>
                </tr>


                <tr>
                    <td class="tlabel" width="10%" align="right">最小金额:</td>
                    <td width="*">
                        {{coupon.minreq}}
                    </td>
                </tr>


                <tr>
                    <td class="tlabel" align="right">过期日期:</td>
                    <td>
                        {{coupon.overdate}}
                    </td>
                </tr>


            </table>


        </el-dialog>
    </div>
</template>


<script type="text/javascript">

    import {VueUtil} from "../util/vueutil";

    export default {

        name: "couponmanager",
        data() {
            return {


                "name": "",


                hostHead: this.$app.contextPath,
                selectedAll: false,
                listCoupon: [],
                pageindex: 1, //初始页
                pagesize: 10,
                total: 10,
                infodlg: false,
                coupon: {}

            }

        },
        methods: {
            pagesizeChange: function (pagesize) {
                this.pagesize = pagesize;
            },
            pageindexChange: function (pageindex) {
                this.pageindex = pageindex;
                console.log(this.pageindex);
                this.search();
            },
            async search() {
                let url = "admin/coupon/list";
                let param = {
                    currentpageindex: this.pageindex,
                    pagesize: this.pagesize,

                    "name": this.name,


                };
                let util = new VueUtil(this);
                console.log("this.pageindex=" + this.pageindex);
                let res = await util.http.post(url, param);
                if (res.data != null) {
                    let pageInfo = res.data.data;
                    this.total = pageInfo.total;
                    this.listCoupon = pageInfo.list;
                    console.log(pageInfo);
                }
            },
            async deleteRec() {
                let url = "admin/coupon/delete";
                let util = new VueUtil(this);
                let hasChecked = this.listCoupon.some(c => {
                    return c.rowSelected == true;
                });
                if (!hasChecked) {
                    util.alert('请选择需要删除的记录', '系统提示', {
                        confirmButtonText: '确定'
                    });
                    return;
                }

                let ids = this.listCoupon.filter(c => c.rowSelected).map(c => c.id);
                let res = util.confirm('是否要删除数据?', '系统提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                }).then(() => {
                    util.http.post(url, {ids}, {emulateJSON: false}).then(res => {
                        if (res.data != null && res.data.stateCode > 0) {
                            this.search();
                        }
                    });
                }).catch(() => {
                });
            },
            exportHandler() {
                window.location.href = this.hostHead + "/admin/coupon/expor";
            },
            selectedAllHandler() {
                console.log(this.selectedAll);
                if (this.listCoupon != null) {
                    this.listCoupon.forEach(c => {
                        c.rowSelected = !this.selectedAll;
                    });
                }
            },
            showInfodlg(coupon) {
                this.coupon = coupon;
                this.infodlg = true;
            },
        },
        created() {
            this.search();
        }
    };

</script>
